import papaparse from 'papaparse';
export const Csv = {
    get playersParserSettings() {
        return {
            delimiter: '\t',
            header: true,
        };
    },
    parsePlayers(players) {
        const lastNewLineIdx = players.lastIndexOf('\n');
        if (lastNewLineIdx !== -1) {
            players = players.substring(0, lastNewLineIdx);
        }
        return papaparse.parse(players, Csv.playersParserSettings);
    },
};
